@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.projectcontainer{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 100px 0;
    align-items: center;
}
.projectcontainer>h1{
    padding-bottom: 15px;
}
.projectcontainer>p{
    padding-bottom: 20px;
}

.projectcontainer_projects{
    display: flex;
    width: 60Vw;
    margin-top: 50px;
    flex-wrap: wrap;
}
@media screen and (max-width:960px){
    .projectcontainer>p{
        text-align: center;
    }
    .projectcontainer{
        margin-top: 0px; 
    }
    .projectcontainer_projects{
        width: 100%;
        max-width: 25em;
        margin-top: 10px;
    }
    .scrooll{
        margin: 20px 0;
    }
}