body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0px;
  
}
a{
  text-decoration: none !important;
  color: white !important;
}
.sticky{
  position: sticky;
  top: 0;
}
.topContent{
    color: white;
    position: relative;
}
.toContent_container{
    position: absolute; 
    top: 160px;
    left: 150px;
    font-weight: 500;
}
.toContent_container >p{
    letter-spacing: 1px;
    text-align: center;
    padding-right: 3px;
    padding-top: 20px;
}
.toContent_container > a > button{
    margin-top: 30px;
    text-align: center;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}
.toContent_container> h1{
    font-size: 50px;
    font-family: "Montserrat",sans-serif;
    font-weight: 900;
}
.toContent_container> h1>span{
    color: #f64c08 !important;
}
.dwn_btn{
    color: white;
    background-color: #f64c08;
    font-size: 14px;
    padding: 10px 18px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;

}
.dwn_btn:hover{
    color: white;
    background-color: teal;
    font-weight: 600;
}
.work_btn{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family:"Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;

}
.work_btn:hover{
    color: white;
    background-color: teal;
    font-weight: 600;
}
.animate__animated.animate__rubberBand{
    --animate-duration: 3s;
}
.animate__animated.animate__headShake{
    --animate-duration: 1s;
}

@media screen and (max-width:960px){
    .topContent{
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        margin: 0px;
    }
    .toContent_container{
      left: 30px;
      top: 250px;
    }
    .toContent_container> h1{
        font-size: 30px;
        font-weight: 600;
    }
    .toContent_container >p{
        text-align: center;
        margin: 0px;
        padding-top: 10px;

    }
    .toContent_container > a > button{
        align-items: center;
        text-align: center;
        justify-content: center;
    }
    .dwn_btn{
        font-size: 12px;
     padding: 8px 16px;
     margin-right: 20px;

    }
    .work_btn{
        font-size: 12px;
        padding: 8px 16px;
        margin-right: 20px;

    }

}
