.navbar{
    display: flex;
 align-items: center;
 color: white;
 background-color:black;
 top: 0;
 position: sticky;
}
.navbar-brand{
    flex: 0.4 1;
    color: white !important;
    text-align: center;
    font-family: "Alata" ,sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}
.navbar-brand>img{
    width: 120%;
}
.navbar-nav{
    display: flex;
    flex: 0.6 1;
    justify-content: space-evenly;
    align-items: center;
    margin-left: auto;
}
.navbar-light .navbar-nav .nav-link {
        font-size: 18px;
        cursor: pointer;
        transition: transfom 500ms ease-in-out !important;
        font-family: "Crimson Text", serif;
        padding: 20px 10px;
        letter-spacing: 1px;
        color: white !important;
      
    }
    .navbar-light .navbar-nav .nav-link:hover{
        -webkit-transform: scale(1.09);
                transform: scale(1.09);
        background-color: #f64c08;
        color: white;
        padding: 2px 10px;
        border-radius: 5px;
        letter-spacing: 1px;
        font-family: "Crimson Text", serif;
        font-size: 18px;
        cursor: pointer;
    }
    .header_rightButtons{
        background-color: #f64c08;
        color: white;
        padding: 2px 1px;
        border-radius: 4px;
        cursor: pointer;
    }
    .navbar-light .navbar-toggler{
        border: none !important;
    }
    .navbar-light .navbar-toggler:focus{
        -webkit-transform: rotate(80deg);
                transform: rotate(80deg);
    }
header.sticky{
