@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');
.space{
    padding: 20px 0;
}
.experienceContainer{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin-top: 100px;
    align-items: center;
}
.experienceContainer >h1{
    padding-bottom: 20px;
    text-shadow:white;
    font-weight: 600;
}

.ques{
    background: url("./pexels.jpg");
    background-size: cover;
    z-index: 1000px;
      /* Set a specific height */
  min-height: 280px; 

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.5);
}
.ques> h5{
    color: #f64c08;
    font-size: 25px;
    font-weight: 900;
    font-family: "Montserrat",sans-serif;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-left: 25px;
}
.ques >p{
    margin-left: 70px;
    font-family: "Montserrat",sans-serif;
    font-weight: 600;
    font-size: 22px;
}
.kk{
    margin-top: 30px;
}
@media screen and (max-width:960px){
    .ques> h5{
    font-size: 18px;
    font-weight: 600;
    margin-left: 18px;
    }
    .ques >p{
        margin-left: 50px;
        font-weight: 500;
        font-size: 16px;
    }
    .kk{
        margin-top: 20px;
    }
    .ques{
        height: 410px;
        width: 90vw;
        margin-left: 15px;
    }
    .experienceContainer{
        margin-top: 10px;
        margin: 10px;
    }
    .space{
       margin: 0px 0;
    }
}
