.tocontainer{
    background: url("./pexels.jpg");
    height: 100vh;
    background-position: center;
    background-size: cover  ;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 2000px rgb(0, 0, 0,0.5);
 
}
@media screen and (max-width:960px){
    .tocontainer{
        height: 78vh;
        background-position: center;
        background-size: cover  ;
        background-repeat: no-repeat;
        box-shadow: inset 0 0 0 2000px rgb(0, 0, 0,0.5);
    }

}
