.contact{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0 100px;
    align-items: center;
}
.contact_cont{
    border: 1px solid white;
    width: 600px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
}
.contact_cont > p{
    font-size: 20px;
    text-align: center;
    padding-bottom: 10px;
}
.contact_cont>p>span{
    color: #f64c08;
}
.contact_icon> a> button{
 color: white;
}
.contact_icon> a > button > span > .MuiSvgIcon-root{
    color: white;
    font-size: 30px;
}
@media screen and (max-width:960px){
    .contact{
        margin: 100px 5px;
    }
    .contact_cont{
       max-width: 400px;
        height: 200px;
    }

}