@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&display=swap');
.navbar{
    display: flex;
 align-items: center;
 color: white;
 background-color:black;
 top: 0;
 position: sticky;
}
.navbar-brand{
    flex: 0.4;
    color: white !important;
    text-align: center;
    font-family: "Alata" ,sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}
.navbar-brand>img{
    width: 120%;
}
.navbar-nav{
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
    margin-left: auto;
}
.navbar-light .navbar-nav .nav-link {
        font-size: 18px;
        cursor: pointer;
        transition: transfom 500ms ease-in-out !important;
        font-family: "Crimson Text", serif;
        padding: 20px 10px;
        letter-spacing: 1px;
        color: white !important;
      
    }
    .navbar-light .navbar-nav .nav-link:hover{
        transform: scale(1.09);
        background-color: #f64c08;
        color: white;
        padding: 2px 10px;
        border-radius: 5px;
        letter-spacing: 1px;
        font-family: "Crimson Text", serif;
        font-size: 18px;
        cursor: pointer;
    }
    .header_rightButtons{
        background-color: #f64c08;
        color: white;
        padding: 2px 1px;
        border-radius: 4px;
        cursor: pointer;
    }
    .navbar-light .navbar-toggler{
        border: none !important;
    }
    .navbar-light .navbar-toggler:focus{
        transform: rotate(80deg);
    }
header.sticky{
    padding: 1px 1px;
    z-index: 99999999;
}
.animate__animated.animate__heartBeat{
    --animate-duration: 1s;
}
.header_rightButtons.nav-link{
    padding: 5px 5px !important;

}
@media screen and (max-width:960px){
    .navbar-light .navbar-nav .nav-link:hover{
        background-color: transparent;
    }

}