@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');
.scrool{
    padding: 20px 0;
}
.skillContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
    overflow: hidden !important;
}
.skillContainer_text{
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}
.skillContainer_text >h2{
    font-size: 30px;
}
.skillContainer_image{
    flex: 0.4;
    text-align: center;
    margin-right: 100px;
}
.skillContainer_image>img{
    width: 600px;
    border-radius: 5px;
    object-fit: contain;
}
.skillContainer_skillset > h5 {
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;
}
.skillContainer_slider > .MuiLinearProgress-colorPrimary
{
    background-color: rgb(88,88,88);
}
.skillContainer1 > div >.MuiLinearProgress-barColorPrimary{
    background-color: tomato;
}
.skillContainer2 > div >.MuiLinearProgress-barColorPrimary{
    background-color: rgb(156,26,3);
}

.skillContainer3 > div >.MuiLinearProgress-barColorPrimary{
    background-color: rgb(225,145,0);
}
.skillContainer4 > div >.MuiLinearProgress-barColorPrimary{ 
    background-color: rgb(228, 213, 0);
}
.skillContainer5 > div >.MuiLinearProgress-barColorPrimary{
    background-color: rgb(136, 146, 0);
}

@media screen and (max-width:960px){
    .skillContainer{
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 80px 0;
        flex-direction: column;
    }
    .skillContainer_text{
    flex: 0.8;
    text-align: left;
    margin-bottom: 20px;  
    align-items: left;
    justify-content: flex-start;
    width: 100%;
    max-width: 22em;
    }
    .skillContainer_image{
        flex: 0.2;
        align-items: center;
        margin-left: 30px;
    }
    .skillContainer_image>img{
        width:400px;
        align-items: center;
        justify-content: center;
        margin-left: 70px;
    }
    .skillContainer_skillset > h5 {
        padding-bottom: 20px;
        padding-top: 10px;
}
.scrool{
    padding: 40px;
}
}


